// Footer.module.scss

.footer {
  background-color: #333;
  color: #fff;
  padding: 20px;
  display: flex;
  left: 0;
  bottom: 0;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.logo img {
  width: 80px; /* Adjust according to your logo size */
  border-radius: 50%;

}

.navigation a {
  color: #fff;
  text-decoration: none;
  margin-right: 20px;
}

.navigation a:hover {
  text-decoration: underline;
}
